.partsContent {
  display: grid;
  grid-template-columns: 24rem 1fr;
  width: 100%;

  &__left {
    &.sole {
      grid-column: span 2;
      max-width: 54rem;
    }
  }

  &__right {
    position: relative;
    padding: 0 0 0 8rem;
    margin: 0 0 0 10rem;

    &::before {
      content: '';
      position: absolute;
      top: .5rem;
      bottom: .5rem;
      left: 0;
      width: .3rem;
      background-color: $gray2;
    }
  }

  h3 {
    margin: 0 0 1.5rem;
    font-size: $fz-xl;
    line-height: 3.5rem;
  }

  p {
    font-size: $fz-s;
    line-height: 2rem;
  }

  .gray {
    color: $gray2;
  }

  .titleBox {
    margin: 0 0 2rem;

    &:last-of-type {
      margin: 0;
    }
  }
}