@import "styles/mixins";
@import "styles/variables";
@import "styles/colors";
@import "styles/fonts";
@import "styles/styles";

* {
  box-sizing: border-box;
  line-height: 1;
}

html {
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
}

body {
  min-height: 100vh;
  margin: 0;
  background-color: $white;
  overflow-x: hidden;
  font-size: $fz-m;
  line-height: 1.12;
}

.App {
  max-width: 192rem;
  padding: 18rem 0 0;
  margin: 0 auto;
}

ul, ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: currentColor;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

button {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
}
