.main {
  position: relative;
  @include contentWidth;

  &__upper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column: span 2;
    margin: 0 0 25rem;
  }

  &__header-box {
    position: relative;
    z-index: 1;
    padding: 14rem 0 0 4rem;
  }

  &__header {
    font-size: 26.5rem;
    color: $black2;
  }

  &__subheader {
    font-size: 17.6rem;
    line-height: 1;
    color: $black2;
  }

  &__desc {
    padding: 3.5rem 0 0;
    font-size: 4.1rem;
    font-weight: 400;
    color: $black2;
  }

  &__year {
    position: relative;
    z-index: 1;
    padding: 0 3.5vw 0 0;
    grid-column-start: 2;
    grid-row-start: 1;
    text-align: right;
    color: rgba($gray, 0.9);
    letter-spacing: 0;
    font-size: 47rem;
    line-height: 42rem;
    font-weight: 700;
  }

  &__map {
    position: absolute;
    z-index: 0;
    top: 5rem;
    right: -3rem;
    max-width: 128rem;
  }

  &__btn-nav {
    position: relative;
    align-self: self-start;
    justify-self: start;
    margin: 0 0 0 4rem;
    color: $gray3;
    font-size: $fz-m;
    letter-spacing: 0.1rem;
    transition: opacity $eia200;

    &:hover {
      opacity: 0.7;
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      bottom: -1.8rem;
      left: .4rem;
      width: 2rem;
      height: 2rem;
      border: .1rem solid $black;
      border-top: none;
      border-left: none;
      transform: rotate(45deg);
    }

    &::after {
      bottom: -2.8rem;
    }
  }

  &__poweredBy {
    justify-self: end;
    width: 18rem;
    margin: 0.8rem 6vw 0 0;
    fill: $gray2;
  }

  &__section-title {
    margin: 0 0 7rem;
    font-size: $fz-xl;
    text-transform: uppercase;
  }

  &__section-subtitle {
    margin: 0 0 4rem;
    font-size: $fz-l;
  }

  &__section-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 9.5rem 13rem;
    max-width: 150rem;
    margin: 0 0 10rem;
  }
}