.barDescMining {
  display: flex;
  flex-direction: column;
  padding: 8.5rem 0 0;

  &__title {
    margin: 0 0 1rem;
    font-size: $fz-s;
    line-height: 2rem;
  }

  &__value,
  &__shift {
    font-size: 4.5rem;
    line-height: 5.5rem;
  }

  &__shift {
    margin: 0 0 1.5rem;
  }

  &__measurement {
    font-size: $fz-s;
    line-height: 2rem;
  }
}