.tabs {
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  background-color: $gray4;
  border-radius: .5rem;

  &.gasPage__stickyNav {
    position: sticky;
    top: 14rem;
    left: 0;
    z-index: 10;
  }

  &.link {
    background-color: transparent;
    border-radius: 0;
  }

  &__item {
    min-height: 2.8rem;
    padding: 0 2rem;
    font-size: $fz-m;
    line-height: 2rem;

    &.active {
      background-color: $black;
      color: $white;
      border-radius: .5rem;

      .link & {
        color: $blue;
        background-color: transparent;
        border-radius: 0;
        -webkit-text-stroke: 1px $blue;
      }
    }
  };
}