.header {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  right: 0;
  min-height: 18rem;
  backdrop-filter: blur(30px);
  background-color: rgba($white, .4);

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem;
    @include contentWidth;
  }

  &__logo-wrapper {
    &.active {
      pointer-events: none;
    }
  }

  &__logo {
    width: 40rem;
    height: 9rem;
    margin: 0 0 2rem;
  }


  &__nav-box {
    display: flex;
    justify-content: space-between;
  }

  &__nav-desc {
    font-weight: 700;
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }


  &__nav-item {
    position: relative;
    margin: 0 0 0 3.4rem;
    transition: color $eia200;

    &:hover,
    &.active {
      color: $purple;
      -webkit-text-stroke: 1px $purple;
    }

    &.active {
      cursor: default;
    }
  }

  &__nav-value {
    .header__nav-item.active & {
      pointer-events: none;
    }
  }

  &__toggle {
    position: absolute;
    top: 3.5rem;
    left: .5rem;
    display: block;
    width: 2rem;
    height: 2rem;
    border-top: .2rem solid $black;
    border-left: .2rem solid $black;
    transform: rotate(-135deg);
    opacity: 1;
    transition: opacity $eia200;

    &.opened {
      pointer-events: none;
      opacity: 0;
    }
  }
}