.barDescField {
  padding: 5rem 0 0;

  &__amount {
    margin: 0 0 1rem;
    font-size: $fz-xxxl;
    letter-spacing: .083rem;
    font-weight: 700;
  }

  &__desc {
    font-size: $fz-s;
    line-height: 2rem;
    margin: 0 0 4rem;
  }

  &__shift {
    font-size: 4.5rem;
    line-height: 5.5rem;
    letter-spacing: .054rem;
    font-weight: 700;
    margin: -2rem 0 .5rem;
  }
}