.subheader {
  position: absolute;
  z-index: 2;
  top: calc(100% - 2.5rem);
  left: 0;
  right: 0;
  @include contentWidth;
  display: flex;
  align-items: center;
  max-width: 130rem;
  padding: 0 5rem 0 9rem;
  margin: 0 auto;
  pointer-events: none;
  opacity: 0;
  transition: opacity $eia200;

  &.visible {
    opacity: 1;
    pointer-events: all;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
  }

  &__desc {
    margin: 0 4rem 0 0;
    font-weight: 700;
  }

  &__toggle {
    position: absolute;
    top: .5rem;
    right: 0;
    display: block;
    width: 2rem;
    height: 2rem;
    border-top: .2rem solid $black;
    border-left: .2rem solid $black;
    transform: rotate(45deg);
  }

  &__value {
    &:hover,
    &.active {
      color: $black;
      -webkit-text-stroke: 1px $black;
    }
  }
}