.output {
  &__link {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
    text-align: left;

    &:disabled {
      color: $black;
      cursor: default;
    }
  }

  &__title-box {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: flex-start;
    grid-column: span 3;
    padding: 0 0 .8rem;
    margin: 0 0 1.2rem;
    border-bottom: .2rem solid $gray2;

    .output__tabs ~ .output__tab-content & {
      padding: 0 0 .6rem;
    }
  }

  &__title {
    padding: 0 1.5rem;
    font-size: $fz-s;
    line-height: 1.75;
    font-weight: 400;
    background-color: $gray4;
    transition: background-color $eia200, color $eia200, border-radius $eia200;

    &:nth-child(2n + 1) {
      border-radius: .5rem 0 0 .5rem;
    }

    &:nth-child(2n) {
      border-radius: 0 .5rem .5rem 0;
    }

    &.active {
      background-color: $black;
      color: $white;
      border-radius: .5rem;
      font-weight: 600;
      transition: background-color 0ms, color 0ms, border-radius 0ms;
    }

    .output__title-box.sole & {
      padding: 0;
      background-color: transparent;
    }
  }

  &__tab-content,
  &__sole-content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    text-align: left;
  }

  &__value-box {
    display: flex;
    flex-direction: column;
  }

  &__value {
    font-size: 4.5rem;
    font-weight: 700;
    line-height: 1;
  }

  &__measurement {
    font-size: $fz-m;
  }

  &__percentage {
    justify-self: flex-end;
    padding: .8rem .8rem 0 0;
    color: $red;
    font-size: 3rem;
    font-weight: 700;

    &.positive {
      color: $green;
    }
  }
}