.chartDesc {
  display: flex;
  flex-direction: column;

  &__title {
    margin: 0 0 1.5rem;
    line-height: 2.4rem;
  }

  &__value,
  &__shift {
    font-size: 4.5rem;
    line-height: 5.5rem;
  }

  &__shift {
    margin: 0 0 2rem;
  }

  &__measurement {
    font-size: $fz-s;
    line-height: 2rem;
  }

  &__button {
    margin: 2rem 0 0;
  }

  &__modal {
    color: $black3;
  }

  &__tableRow {
    display: grid;
    grid-template-columns: 4fr 1fr 15%;
    align-items: center;

    &--title {
      grid-template-columns: 1fr 15%;
      border-bottom: .1rem solid $purple;
    }

    &--footer {
      margin: 0 0 2rem;
      border-top: .1rem solid $gray6;
      border-bottom: .1rem solid $gray6;
    }
  }

  &__cell {
    font-size: $fz-l;
    line-height: 3rem;
    justify-self: center;
    padding: 1rem;

    &--wide {
      justify-self: flex-end;
      padding: 1rem 9rem;
    }
  }

  &__cellName,
  &__cellValue,
  &__cellShift {
    padding: 1rem 0;

    .chartDesc__tableRow--footer & {
      font-weight: 700;
    }
  }

  &__cellName {
    font-size: $fz-s;
    line-height: 3rem;
  }

  &__cellValue {
    justify-self: center;
  }

  &__cellShift {
    justify-self: center;
    color: $red2;

    &.plus {
      color: $green;
    }

    &--none {
      color: $black3;
    }
  }
}