.fieldOverview {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 192rem;
  min-height: 93rem;
  margin: 0 auto;
  overflow: hidden;

  .gasPage & {
    margin: 0 auto 8.5rem;
  }

  &__bg {
    position: absolute;
    z-index: 0;
    top: -12rem;
    left: 50%;
    width: 194rem;
    object-fit: cover;
    transform: translateX(-50%);
  }

  &__toggle {
    margin: 0 0 4.2rem;
  }

  &__content {
    position: relative;
    z-index: 1;
    flex-grow: 1;
    display: grid;
    grid-template-columns: 42% 1fr;
    width: 100%;
    max-width: 185rem;
    min-height: 100%;
    padding: 0 3rem 14rem;
    margin: 0 auto;
  }

  &__title {
    position: relative;
    padding: 30rem 2rem 30rem 7.5rem;
    color: $black2;
    text-transform: uppercase;
    backdrop-filter: blur(1.5rem);

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba($white, .4);
    }
  }

  &__title-main {
    position: relative;
    z-index: 1;
    font-size: 13rem;
    letter-spacing: 0.156rem;
    margin: 0 0 1.4rem;
  }

  &__title-sub {
    position: relative;
    z-index: 1;
    font-size: $fz-xxl;
    line-height: 8.7rem;
    letter-spacing: 0.44rem;
    font-weight: 400;
  }

  &__inner {
    position: relative;
    padding: 23.5rem 10rem 10rem 15rem;
    backdrop-filter: blur(3rem);
    border-bottom-right-radius: 3rem;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba($white, .4);
      border-bottom-right-radius: 3rem;
    }
  }

  &__rate-list {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template: auto auto/auto auto;
    grid-gap: 3.5rem 4rem;
    color: $black2;
  }

  &__rate-title {
    font-size: $fz-m;
    letter-spacing: .135rem;
    text-transform: uppercase;
  }

  &__rate-value {
    font-size: $fz-xxl;
    line-height: 5rem;
    letter-spacing: .413rem;
  }

  &__rate-measurement {
    font-size: $fz-s;
    line-height: 6.7rem;
  }

  &__slider {
    position: relative;
    z-index: 1;
  }

  &__slider-toggle {
    width: 1.4rem;
    height: 1.4rem;
    margin: 0 0 3rem;
    border-radius: 50%;
    background-color: transparent;
    border: .1rem solid $black;
    transition: background-color $eia200;

    &:not(:last-of-type) {
      margin: 0 1.2rem 0 0;
    }

    &.active {
      background-color: $black;
    }
  }

  &__slider-list-wrapper {
    display: none;
    max-height: 35rem;
    padding: 0 3.5rem 0 0;
    font-size: $fz-s;
    overflow: auto;

    &.active {
      display: block;
    }
  }

  &__slider-text,
  &__slider-list-item {
    margin: 0 0 3rem;
    line-height: 1.4;
  }

  &__slider-list-item {
    position: relative;
    padding: 0 0 0 1.2rem;

    &::before {
      content: '';
      position: absolute;
      top: .8rem;
      left: 0;
      width: .5rem;
      height: .5rem;
      background-color: $black;
    }
  }
}