.gasPage {
  position: relative;
  
  &__sectionTitle-box {
    display: grid;
    grid-template-columns: 100rem 1fr;
    @include contentWidth;
    align-items: baseline;
    justify-items: flex-end;
    padding: 1rem 0 0;
    border-top: .1rem solid $purple;
  }

  &__sectionTitle {
    @include contentWidth;
    margin: 0 auto 6.5rem;
    font-size: $fz-xxl;
    line-height: 7.5rem;
    color: $black;
    text-transform: uppercase;
    letter-spacing: 1.03rem;

    .gasPage__sectionTitle-box & {
      margin: 0;
      justify-self: flex-start;
    }

    &.active {
      position: sticky;
      top: 10rem;
      left: 0;
      z-index: 10;
    }
  }

  &__content {
    padding: 0 0 0 25rem;

    &.gasPage__sectionField {
      margin: 0 0 4rem;
    }
  }

  &__subTitle {
    @include contentWidth;
    display: grid;
    grid-template: 1fr 1fr/ fit-content(10rem) 1fr;
    grid-gap: 3rem 2rem;
    align-items: baseline;
    padding: 0 0 0 1rem;
  }

  &__subTitle-amount {
    font-size: $fz-xxxl;
    letter-spacing: .083rem;
    color: $purple;
  }

  &__subTitle-value {
    font-size: $fz-xl;
    line-height: 4rem;
    color: $black;
    text-transform: uppercase;
    font-weight: 400;
  }

  &__subTitle-desc {
    font-size: $fz-s;
    line-height: 2rem;
    grid-column: 2/3;
  }

  &__innerTitle {
    grid-column: span 2;
    justify-self: flex-start;
    padding: 6rem 0 0 13rem;
    margin: 0 0 2rem;
  }

  &__innerTabs {
    justify-self: flex-start;
    margin: 0 0 1.5rem 13rem;
  }

  &__tabContent {
    grid-column: span 2;
    max-width: 120rem;
    width: 100%;
    justify-self: flex-start;
  }

  &__barVertChart {
    padding: 0 0 0 12.5rem;
  }

  &__lineChart {
    padding: 4rem 0 0 12.5rem;
    margin: 0 0 4rem;
  }

  &__partsContent {
    grid-column: span 2;
    margin: 0 0 6rem;
  }

  &__productionParts {
    width: 100rem;
    grid-column: span 2;
    justify-self: flex-start;
    padding: 6rem 0 0;
  }

  &__pieChart {
    grid-column: span 2;
    width: 100%;
  }
}