.Modal {
  position: relative;
  display: flex;
  flex-direction: column;
  max-height: calc(100vh - 6.4rem);
  max-width: 100%;
  padding: 6rem;
  color: $black;
  background: $white;
  border-radius: 0.4rem;

  &__background {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba($white, 0.4);
    backdrop-filter: blur(29px);
    overflow: hidden;
    -webkit-overflow-scrolling: touch;
    overflow-y: auto;
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-height: 100%;
    min-height: 100%;
    padding: 3.2rem;
    margin: auto;
  }

  &__title {
    padding: 0 3.2rem;
    font-size: 2rem;
    line-height: 2.8rem;
  }

  &__close {
    position: absolute;
    top: 8rem;
    right: 2rem;
    width: 4rem;
    height: 4rem;
    background-color: $gray5;
    border-radius: 50%;
    transition: opacity $eia250;

    &:hover {
      opacity: .7;
    }

    &::before {
      content: '';
      position: absolute;
      top: 1.8rem;
      left: .5rem;
      width: 3rem;
      height: 0.3rem;
      background-color: $white;
      transform: rotate(45deg);
    }

    &::after {
      content: '';
      position: absolute;
      top: 1.8rem;
      left: .5rem;
      width: 3rem;
      height: 0.3rem;
      background-color: $white;
      transform: rotate(-45deg);
    }
  }

  &__content {
    width: 70vw;
    padding: 0 3.2rem;
    margin: 1.6rem 0 2.4rem;
    overflow: auto;
  }

  &__bottomBar {
    display: flex;
    flex-direction: row-reverse;
    padding: 0 3.2rem;
    width: 100%;
  }
}